<template>
  <div class="research">
    <nav-bar></nav-bar>
    <mask-list></mask-list>
    <div :class="{'banner':true,'bannerTop':!(isiOS||isAndroid)}">
      <div class="container">
        <div class="row">
          <h1>{{$t('researchLanguge.title')}}</h1>
          <div class="bannerBgc">
          </div>
        </div>
      </div>
    </div>
    <div class="content">
      <div class="container">
        <div class="row">
          <div 
          v-if="leftPress || leftNormal"
          :class="{'leftBtn':true, 'leftBtnBgcPress':leftPress, 'leftBtnBgcNormal':leftNormal}"
          @click="clickLeftBtn"></div>
          <div 
          v-if="rightPress || rightNormal"
          :class="{'rightBtn':true, 'rightBtnBgcPress':rightPress, 'rightBtnBgcNormal':rightNormal}"
          @click="clickRightBtn"
          ></div>
          <div class="col-xs-12 col-md-3 containerTabBar" ref="tabScroll">
            <div class="nillDom hidden-xs hidden-sm"></div>
            <ul class="containerTabBarInfo">
              <router-link to="/research/international" tag="li" class="scrollLiWidth" active-class="active" @click.native="handleClickEvent('86717','click_research_international')"><p>{{$t('researchLanguge.researchAndMade.title')}}</p></router-link>
              <router-link to="/research/audioTransmission" class="scrollLiWidth" tag="li" active-class="active" @click.native="handleClickEvent('82091','click_research_audio_and_video')"><p>{{$t('researchLanguge.transfer.title')}}</p></router-link>              
              <router-link to="/research/newMedia" tag="li" class="scrollLiWidth" active-class="active" @click.native="handleClickEvent('86718','click_research_immersive')"><p>{{$t('researchLanguge.newMedia.title')}}</p></router-link>
              <router-link to="/research/videoCode" tag="li" class="scrollLiWidth" active-class="active" @click.native="handleClickEvent('86719','click_research_codec')"><p>{{$t('researchLanguge.VideoCodingAndDecoding.title')}}</p></router-link>
              <router-link to="/research/audioProcessing" tag="li" class="scrollLiWidth" active-class="active" @click.native="handleClickEvent('86716','click_research_audio')"><p>{{$t('researchLanguge.audioHandle.title')}}</p></router-link>
              <router-link to="/research/videoProcessing" tag="li" class="scrollLiWidth" active-class="active" @click.native="handleClickEvent('82088','click_research_process')"><p>{{$t('researchLanguge.videoHandle.title')}}</p></router-link>
              <router-link to="/research/perceptual" tag="li" class="scrollLiWidth" active-class="active" @click.native="handleClickEvent('86720','click_research_perceived')"><p>{{$t('researchLanguge.touch.title')}}</p></router-link>
              <router-link to="/research/assessment" tag="li" class="scrollLiWidth" active-class="active" @click.native="handleClickEvent('82092','click_research_assess')"><p>{{$t('researchLanguge.connectionQuality.title')}}</p></router-link>                            
              <router-link to="/research/VideoUnderstanding" tag="li" class="scrollLiWidth" active-class="active" @click.native="handleClickEvent('82087','click_research_understanding')"><p>{{$t('researchLanguge.videoUnderstanding.title')}}</p></router-link>              
            </ul>
          </div>
          <router-view class="col-xs-12 col-sm-12 col-md-9 col-lg-9"></router-view>
        </div>
      </div>
    </div>
    <foot-bar></foot-bar>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { handleClickEvent } from '../../util/util.js';
import NavBar from '../../components/NavBar';
import MaskList from '../../components/mask';
import footBar from "../../components/foot";

export default {
  components: {
    NavBar,
    MaskList,
    footBar
  },
  data() {
    return {
      cunrrent: 0,
      leftPress: false,
      leftNormal: false,
      rightPress: true,
      rightNormal: false,
    }
  },
  computed: {
    ...mapState({
        isMask: state => state.isMask,
        isAndroid: state => state.isAndroid,
        isiOS: state => state.isiOS,
        scrollIndex: state => state.scrollIndex
    }),
    moveScroll:function() {
      return this.$refs.tabScroll.scrollLeft
    },
    title:function () {
      return this.$t('researchLanguge.researchAndMade.title')
    }
  },
  watch: {
    title: function(val) {
      const containerTabBarInfo = document.querySelector('.containerTabBarInfo');
      const elementDom = document.querySelectorAll('.scrollLiWidth');
      let width = 0;
      const u = navigator.userAgent;
      const isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; 
      const isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); 
      if(isAndroid || isiOS) {
          this.$nextTick(() => {
          for(let i=0;i<elementDom.length;i++) {
          width+= elementDom[i].offsetWidth
        }
          containerTabBarInfo.style.width = width+200+'px'
        })
      }
    },
    isMask: function(val) {
      const indexH = document.querySelector('.research');
      const mainH = document.documentElement.clientHeight;
      this.$nextTick(() => {
        indexH.style.height =val? mainH +'px' : 100 +'%';
        indexH.style.overflow = val? 'hidden' : 'visible';

        this.$refs.tabScroll.addEventListener('scroll', this.menu)
      let initMoveWidth = 0;
      const elementDom = document.querySelectorAll('.scrollLiWidth');
      if(this.scrollIndex == 8) {
        this.SET_SCROLLINDECX(7)
      }
      for(let i=0;i<this.scrollIndex;i++) {
        initMoveWidth+= elementDom[i].offsetWidth
      }
      this.common(initMoveWidth,'left')
      })
    }
  },
  mounted() {
    const containerTabBarInfo = document.querySelector('.containerTabBarInfo');
      const elementDom = document.querySelectorAll('.scrollLiWidth');
      const u = navigator.userAgent;
      const isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; 
      const isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); 
      let width = 0;
      for(let i=0;i<elementDom.length;i++) {
        width+= elementDom[i].offsetWidth
      }
    this.$nextTick(() => {
      
      this.$refs.tabScroll.addEventListener('scroll', this.menu)
      let initMoveWidth = 0;
      const elementDom = document.querySelectorAll('.scrollLiWidth');
      if(this.scrollIndex == 8) {
        this.SET_SCROLLINDECX(7)
      }
      for(let i=0;i<this.scrollIndex;i++) {
        initMoveWidth+= elementDom[i].offsetWidth
      }
      this.common(initMoveWidth,'left')
      if(isiOS|| isAndroid) {
        containerTabBarInfo.style.width = width+200+'px'
      }
      
    })
  },
  methods: {
    ...mapMutations(['SET_SCROLLINDECX']),
    // 埋点
    handleClickEvent(id, eventName, params) {
      handleClickEvent(id, eventName, params)
    },
    menu() {
      if(this.$i18n.locale == 'zh') {
        const leftWidth = this.$refs.tabScroll.scrollLeft
        this.$nextTick(() => {
          if(leftWidth>0 && leftWidth<439) {
            this.leftPress = false;
            this.leftNormal = true;
            this.rightNormal = false;
            this.rightPress = true;
          }else if(leftWidth==0) {
            this.leftPress = false;
            this.leftNormal = false;
            this.rightPress = true;
            this.rightNormal = false;
          }else if(leftWidth==439) {
            this.leftPress = true;
            this.leftNormal = false;
            this.rightPress = false;
            this.rightNormal = true;
          }
        })
      }else {
        const leftWidth = this.$refs.tabScroll.scrollLeft
        this.$nextTick(() => {
          if(leftWidth>0 && leftWidth<1152) {
            this.leftPress = false;
            this.leftNormal = true;
            this.rightNormal = true;
            this.rightPress = true;
          }else if(leftWidth==0) {
            this.leftPress = false;
            this.leftNormal = false;
            this.rightPress = true;
            this.rightNormal = true;
          }else if(leftWidth==1152) {
            this.leftPress = true;
            this.leftNormal = false;
            this.rightPress = false;
            this.rightNormal = true;
          }
        })
      }
      
    },
    common(initMoveWidth,leftOrRight) {
      if(this.$i18n.locale == 'zh') {
        this.$nextTick(() => {
          this.$refs.tabScroll.scrollLeft = initMoveWidth
          const leftWidth = this.$refs.tabScroll.scrollLeft
          if(leftWidth>0 && leftWidth<439) {
            if(leftOrRight == 'left') {
              this.leftPress = false;
              this.leftNormal = true;
              this.rightNormal = false;
              this.rightPress = true;
            }else {
              this.leftPress = false;
              this.leftNormal = true;
              this.rightNormal = false;
              this.rightPress = true;
            }
          }else if(leftWidth==0) {
            this.leftPress = false;
            this.leftNormal = false;
            this.rightPress = true;
            this.rightNormal = false;
          }else if(leftWidth==439) {
            this.leftPress = true;
            this.leftNormal = false;
            this.rightPress = true;
            this.rightNormal = false;
          }
        })
      }else {
        this.$nextTick(() => {
          this.$refs.tabScroll.scrollLeft = initMoveWidth
          const leftWidth = this.$refs.tabScroll.scrollLeft
          if(leftWidth>0 && leftWidth<1152) {
            if(leftOrRight == 'left') {
              this.leftPress = false;
              this.leftNormal = true;
              this.rightNormal = true;
              this.rightPress = false;
            }else {
              this.leftPress = false;
              this.leftNormal = true;
              this.rightNormal = true;
              this.rightPress = false;
            }
          }else if(leftWidth==0) {
            this.leftPress = false;
            this.leftNormal = false;
            this.rightPress = false;
            this.rightNormal = true;
          }else if(leftWidth==1152) {
            this.leftPress = true;
            this.leftNormal = false;
            this.rightPress = false;
            this.rightNormal = true;
          }
        })
      }
    },
    clickLeftBtn() {
      let initMoveWidth = 0;
      if(this.scrollIndex>0) {
        this.SET_SCROLLINDECX(this.scrollIndex-1)
      }
      const elementDom = document.querySelectorAll('.scrollLiWidth');
       for(let i=0;i<this.scrollIndex;i++) {
        initMoveWidth+= elementDom[i].offsetWidth
      }
      this.common(initMoveWidth, 'left')
    },
    clickRightBtn() {
      let initMoveWidth = 0;
      if(this.scrollIndex<=7) {
        this.SET_SCROLLINDECX(this.scrollIndex+1)
      }
      const elementDom = document.querySelectorAll('.scrollLiWidth');
       for(let i=0;i<this.scrollIndex;i++) {
        initMoveWidth+= elementDom[i].offsetWidth
      }
      this.common(initMoveWidth, 'right')
    }
  },
  beforeDestroy() {
    this.$refs.tabScroll.removeEventListener('scroll', this.menu)
  }
}
</script>

<style lang="scss">
@import '../../assets/css/research.scss';
</style>
